// context/AuthContext.tsx

"use client";

import React, { createContext, useContext, useEffect, useState } from "react";
import { User, onIdTokenChanged } from "firebase/auth";
import { auth, db } from "@/lib/firebase"; // lib/firebase.ts 파일
import { doc, getDoc } from "firebase/firestore";

// 관리자 권한을 포함한 사용자 정보 인터페이스
export interface AuthUser extends User {
  isAdmin?: boolean;
}

interface AuthContextType {
  user: AuthUser | null;
  loading: boolean;
  isFirstLogin: boolean | null; // 첫 로그인 여부 (null: 확인 전)
  checkFirstLogin: (user: User) => Promise<boolean>; // 첫 로그인 체크 함수
}

const AuthContext = createContext<AuthContextType>({
  user: null,
  loading: true,
  isFirstLogin: null,
  checkFirstLogin: async () => false,
});

// Context Provider 컴포넌트
export const AuthProvider = ({ children }: { children: React.ReactNode }) => {
  const [user, setUser] = useState<AuthUser | null>(null);
  const [loading, setLoading] = useState(true);
  const [isFirstLogin, setIsFirstLogin] = useState<boolean | null>(null);

  useEffect(() => {
    // 인증 상태 리스너
    const unsubscribe = onIdTokenChanged(auth, async (firebaseUser) => {
      setLoading(true);
      setIsFirstLogin(null); // 유저가 바뀌면 초기화

      if (firebaseUser) {
        // [관리자 권한 확인]
        // 1단계에서 백엔드(Functions)가 심어준 Custom Claim을 확인합니다.
        const idTokenResult = await firebaseUser.getIdTokenResult();
        const isAdmin = idTokenResult.claims.admin === true;

        // 관리자 여부를 포함한 AuthUser 객체 생성
        const authUser: AuthUser = {
          ...firebaseUser,
          isAdmin: isAdmin,
        };
        
        setUser(authUser);
        
        // [첫 로그인 여부 확인]
        await checkFirstLogin(firebaseUser);

      } else {
        setUser(null);
      }
      setLoading(false);
    });

    return () => unsubscribe(); // 클린업
  }, []);

  /**
   * Firestore의 'users' 컬렉션을 확인하여 프로필이 존재하는지 체크합니다.
   * @param firebaseUser 인증된 사용자 객체
   * @returns 첫 로그인이면 true, 아니면 false
   */
  const checkFirstLogin = async (firebaseUser: User): Promise<boolean> => {
    const userDocRef = doc(db, "users", firebaseUser.uid);
    const docSnap = await getDoc(userDocRef);
    
    const isNew = !docSnap.exists();
    setIsFirstLogin(isNew);
    return isNew;
  };

  const value = {
    user,
    loading,
    isFirstLogin,
    checkFirstLogin,
  };

  return (
    <AuthContext.Provider value={value}>
      {!loading && children} {/* 로딩이 끝나면 자식 컴포넌트 렌더링 */}
    </AuthContext.Provider>
  );
};

// Context를 쉽게 사용하기 위한 Custom Hook
export const useAuth = () => {
  return useContext(AuthContext);
};